<template>
    <div id="wishlist" class="wishlist-container container-fluid" v-bind:class="{ 'fix-wish': scrollPosition > 0 }" >
        <div class="wishlist container">
            <div class="wishlist-header">
                <span>
                    wish list
                </span>
            </div>
            <div class="wish-list">
                <div class="list-header d-sm-block d-none">
                    <ul>
                        <li class="col-md-5 col-sm-4 col-10">product</li>
                        <li class="col-sm-2 col-1 d-sm-inline-block d-none">price</li>
                        <li class="col-sm-2 col-2 d-sm-inline-block d-none">stock statu</li>
                        <li class="col-md-2 col-sm-3 d-sm-inline-block d-none">action</li>
                        <li class="col-sm-1 col-2" style="text-align: center">X</li>
                    </ul>
                </div>
                <div class="list">
                    <div class="product col-12" v-for="item in items" :key="item.id">
                        <div class="item-info col-md-5 col-sm-4 col-12">
                            <div class="item-photo col-sm-4 col-4">
                                <img :src="'https://queen-store.net/azshop/public_html/board/public_html/items-photos/' +  item.photo_link" alt="">
                            </div>
                            <div class="item-name col-sm-8 col-8">
                                <h5>{{item.name}}</h5>
                                <span>{{item.category}}</span>
                            </div>
                        </div>
                        <div class="item-price col-sm-2 col-1 d-sm-block d-none">
                            <span>{{item.selling_price}} L.E</span>
                        </div>
                        <div class="stock-status col-sm-2 col-2 d-sm-block d-none">
                            <span v-if="item.quantity > 0">in stock</span>
                        </div>
                        <div class="add-to-cart col-md-2 col-sm-3 col-12">
                          <router-link :to="'/product/'+item.id">

                            <button>View Product</button>
                         </router-link>

 
                            <button @click="addItemToCart(item.id,item.color,item.size)" class="d-none">add to cart</button>
                        </div>
                        <div class="remove-item col-sm-1 col-2" @click="warning = true , deletedItem = item.id">
                            <span>x</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="delete-product-pop-up" v-if="warning">
            <div class="messege-pop-up">
                <div class="messege-title">
                    <span> Are you sure ?</span>
                </div>
                <div class="messege-action">
                    <button @click="warning = false">cancel</button>
                    <button @click="deleteWishItem() , warning = false">delete</button>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
@import "../style/css/scss/views/_wish.scss";
</style>
<script>
import axios from 'axios';
export default {
    name: "wishlist",
    
    el: "#wishlist",

    data() {
        return {
            scrollPosition: null,
            user_token:"",
            items:[],
            warning:false,
            deletedItem:""
        };
    },
    methods: {
        addItemToCart(id,color,size){
            this.user_token = localStorage.getItem("user_token");
            axios
            .get(
                'https://queen-store.net/azshop/public_html/board/public_html/api/storecart', 
                {
                    headers: {'Authorization': 'Bearer ' + this.user_token },
                    params:{
                        items_ids:[id],
                        quantity:["1"],
                        color:[color[3]],
                        size:[size[3]]
                    }
                }
            ).then(() => {
                axios.get(('https://queen-store.net/azshop/public_html/board/public_html/api/carts') , {headers: {'Authorization': `Bearer ` + this.user_token}})
                .then((res) => {
                    this.$parent.cartItemsNo = res.data.items.length;
                    this.$parent.cartItems = res.data.items;
                    this.$parent.cartTotalPrice = 0;
                    for(var i = 0 ; i < this.$parent.$parent.cartItems.length ; i++){
                        this.$parent.cartTotalPrice = this.$parent.cartTotalPrice + (this.$parent.cartItems[i].selling_price * this.$parent.cartItems[i].quantity);
                    }
                })
            })
        },
        updateScroll() {
            this.scrollPosition = window.scrollY;
        },
        deleteWishItem(){
            axios.delete(
                'https://queen-store.net/azshop/public_html/board/public_html/api/wishlist/' + this.deletedItem,
                {headers: {'Authorization': `Bearer ` + this.user_token}}
            ).then(() => {
                axios.get(
                    ("https://queen-store.net/azshop/public_html/board/public_html/api/wishlist"),
                    {headers: {'Authorization': 'Bearer ' + this.user_token }}            
                ).then((res) => {
                    this.items = res.data.items
                    this.$parent.wishListNo = res.data.items.length
                });
            });
        }
    },
    mounted() {
        window.addEventListener("scroll", this.updateScroll);
        this.user_token = localStorage.getItem("user_token");
        axios.get(
            ("https://queen-store.net/azshop/public_html/board/public_html/api/wishlist"),
            {headers: {'Authorization': 'Bearer ' + this.user_token }}            
        ).then((res) => {
            this.items = res.data.items
        });
    }
};
</script>
